*,
*::after,
*::before {
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
}

body {
  font-family: "Lato", sans-serif;
  box-sizing: border-box;
  margin: 0;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

img {
  display: block;
  width: 100%;
}

.container {
  width: 80%;
  max-width: 1200px;
  margin: 0 auto;
}

.section__header {
  text-align: center;
  margin: 5rem 0;
}

.header {
  background-image: linear-gradient(90deg, rgba(213, 32, 71, 0.8) 0%, rgba(238, 61, 27, 0.8) 100%), url(/img/hero.jpg);
  background-size: cover;
  height: 70rem;
}

.col-2 {
  flex: 1 1 40rem;
}

.navbar {
  font-size: 1.4rem;
  color: #fff;
  display: flex;
  flex-wrap: wrap;
  padding-top: 5rem;
  padding-bottom: 1.7rem;
  transition: transform 1s;
  justify-content: space-between;
  align-items: center;
}
.navbar__logo a {
  font-size: 1.6rem;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
}
.navbar__logo img {
  width: 3rem;
}
.navbar__logo p {
  text-transform: uppercase;
  font-weight: 700;
  margin-left: 0.652rem;
}
.navbar__menu {
  font-weight: 400;
  display: flex;
}
.navbar__menu.openMenuMobile {
  background: rgba(19, 19, 19, 0.8);
  transform: translateY(0);
  opacity: 1;
  transition: all 0.5s ease;
}
.navbar__menu li {
  margin: 1rem;
}
.navbar__menu li a {
  color: #fff;
  text-transform: uppercase;
}
.navbar__location {
  display: none;
  font-weight: 400;
}
.navbar__location li a {
  color: #fff;
  text-transform: uppercase;
}
.navbar__hamburger {
  display: none;
  flex-direction: column;
  cursor: pointer;
}
.navbar__hamburger span {
  display: block;
  background: #fff;
  width: 2rem;
  height: 0.265rem;
  margin-top: 0.265rem;
  margin-bottom: 0.265rem;
  transition: all 250ms ease;
}
.navbar__hamburger.active span:nth-child(1) {
  margin: 8px auto;
  transform: translateY(13px) rotate(45deg);
}
.navbar__hamburger.active span:nth-child(2) {
  opacity: 0;
}
.navbar__hamburger.active span:nth-child(3) {
  margin: 8px auto;
  transform: translateY(-13px) rotate(-45deg);
}